<template>
  <div class="url-container" v-loading="isLoading">
    <div class="url-form">
      <div class="form-item">
        <div class="form-label">URL</div>
        <div class="form-control">
          <Input placeholder="为正常使用二维码，请输入app/小程序URL" style="width: 100%"
                 v-model="url"/>
        </div>
      </div>
      <div class="form-item">
        <div class="form-label"></div>
        <div class="form-control">
          <button @click="handleSubmit" class="gym-btn primary-btn">确定</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getUrl, setUrl } from 'api/setting'

export default {
  data () {
    return {
      isLoading: true,
      url: '',
      id: 0
    }
  },
  mounted () {
    getUrl({}).then(res => {
      if (Number(res.code) === 200) {
        this.url = res.data ? res.data.url : ''
        this.id = res.data ? res.data.id : 0
      } else {
        this.$message({
          message: res.message,
          type: 'error'
        })
      }
    }).finally(() => {
      this.isLoading = false
    })
  },
  methods: {
    handleSubmit () {
      this.isLoading = true
      setUrl({
        url: this.url,
        id: this.id
      }).then(res => {
        if (Number(res.code) === 200) {
          this.$message({
            message: '修改成功',
            type: 'success'
          })
        } else {
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      }).finally(() => {
        this.isLoading = false
      })
    }
  }
}
</script>
<style lang='stylus' scoped>
  .url-container
    width 100%
    height 100%
    display flex
    align-items center
    justify-content center
  .url-form
    font-size 14px
    width 100%
    max-width 600px
    padding 0 20px
    position relative
    top -80px
  .form-item
    display flex
    align-items stretch
    margin-bottom 30px
  .form-label
    color #333333
    width 60px
    display flex
    align-items center
    justify-content flex-end
    padding-right 20px
  .form-control
    flex 1
  .confirm-button
    width 90px
    height 36px
    background #6494DD
    color white
    border-radius 4px
  .url-form /deep/ .ivu-input
    height 36px
    font-size 14px
</style>



